import React from 'react';
import { NavLink } from 'react-router-dom';

const Navbar = () => {
    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-white bg-white px-5">
                <div className="container-fluid nav-div">
                    <a className="navbar-brand text-warning fs-2" href="#">Dauth Gurung</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                            <NavLink exact activeClassName="active_class" className="nav-link" to='/'>Home</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink exact activeClassName="active_class" className="nav-link" to='/about'>About Me</NavLink>
                            </li>
                            <li className="nav-item">
                            <NavLink exact activeClassName="active_class" className="nav-link" to='/portfolio'>Portfolio</NavLink>
                            </li>
                            <li className="nav-item">
                            <NavLink exact activeClassName="active_class" className="nav-link" to='/contact'>Contact Me</NavLink>
                            </li>
                        </ul>
                        
                    </div>
                </div>
            </nav>

        </>
    )
}
export default Navbar;